.App {
  text-align: center;
  background-color: white;
  color:white;
  min-height: 100vh;
}

.footer{
  background-color: rgb(160, 239, 4);
}

h1{
  color: black;
  margin-bottom: 10;
}

.header{
  
  background-color:#FF69B4;
  padding:8px;
}

.main{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding :14px;
  background-color: #FCEDDA;
  align-items: center;
}
.movie{
  background-color: #00FFFF;
  color:black;
  border: 1px solid gray;
  padding : 3px;
  font-weight: bold;
  text-align: center;
  width: 20%;
  margin-top: 14px;
  align-self: normal;
  display: flex;
  flex-direction: column;
}

.movie img{
  width: 100%;
  height: 70%;
}

button{
  margin: 10px;
  background-color: aqua;
  padding: 10px;
  border-radius: 15px;
  display: inline;
  align-self: center;
}
.desc{
  font-size: 1rem;
}


@media screen and (max-width: 480px) {
  .movie{
    background-color: #00FFFF;
    color:black;
    border: 1px solid gray;
    font-weight: bold;
    text-align: center;
    margin-top: 14px;
    align-self: normal;
    display: flex;
    flex-direction: column;
    padding : 2px;
    width: 20%;
    height: 50%;
  }
  .movie img{
    width: 100%;
    height: 10%;
  }
  p{
    font-size: 0.2rem;
  }
  .desc{
    font-size: 0.5rem;
  } 
}